// SECTION facilities :: START

.facilities {
  &__main {
    height: 700px;
    padding-bottom: 100px;
    background: #000000;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;

    .c-grid {
      height: 100%;
    }

    .facilities__wrapper {
      display: flex;
      align-items: flex-end;
      height: 100%;
    }

    &-row {
      display: flex;
      align-items: center;
      width: 100%;

      > div {
        width: 50%;

        &:nth-of-type(1) {}

        &:nth-of-type(2) {
          display: flex;
          justify-content: flex-end;
        }
      }
    }

    &-link {
      display: flex;
      align-items: center;
      margin-bottom: 60px;

      svg {
        width: 14px;
        height: 14px;
        fill: #fff;
      }

      span {
        padding-left: 20px;
        font-size: 19px;
        color: #FFFFFF;
        letter-spacing: 0;
        line-height: 30px;
      }
    }

    &-title {
      padding-bottom: 20px;
      font-weight: 200;
      font-size: 50px;
      color: #FFFFFF;
      letter-spacing: 0;
      line-height: 60px;
    }

    &-badge {
      display: inline-flex;
      align-items: center;
      justify-content: center;
      min-height: 30px;
      padding: 5px 20px;
      border-radius: 15px;
      background-color: #F5A623;
      font-weight: bold;
      text-transform: uppercase;
      font-size: 16px;
      color: #FFFFFF;
      letter-spacing: 2px;
      text-align: center;
    }

    &-btn {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100%;
      max-width: 216px;
      height: 70px;
      font-weight: bold;
      text-transform: uppercase;
      font-size: 18px;
      color: #FFFFFF;
      letter-spacing: 1.35px;
      text-align: center;
      line-height: 32px;
      background-color: #CD084B;
      border: 2px solid #cd084b;
      border-radius: 10px;
      @include transition(color, background-color);

      &:hover {
        color: #CD084B;
        background-color: transparent;
        @include transition(color, background-color);
      }
    }
  }

  &__description {
    padding-top: 80px;
    padding-bottom: 80px;

    p {
      padding-bottom: 30px;
      font-size: 17px;
      color: #575757;
      letter-spacing: 0;
      line-height: 26px;
    }

    h6 {
      display: flex;
      align-items: center;
      min-height: 66px;
      margin-bottom: 40px;
      padding: 10px 30px;
      border-radius: 10px;
      background-color: #f2f5fa;
      font-size: 17px;
      font-weight: normal;
      color: #575757;
      letter-spacing: 0;
      line-height: 26px;
    }

    h2 {
      padding-bottom: 10px;
      font-weight: normal;
      font-size: 26px;
      color: #000000;
      letter-spacing: 0;
      line-height: 50px;
    }

    ul {
      padding-left: 15px;

      li {
        padding-left: 20px;
        padding-bottom: 15px;
        list-style-type: lower-alpha;
        font-size: 17px;
        color: #575757;
        letter-spacing: 0;
        line-height: 26px;
      }
    }
  }

  &__share {
    padding-bottom: 80px;

    .facilities__wrapper {
      display: flex;
      align-items: center;
      justify-content: center;
      min-height: 100px;
      border-radius: 10px;
      background-color: #f2f5fa;

      > p {
        padding-right: 30px;
        font-weight: bold;
        font-size: 18px;
        color: #000;
        text-transform: uppercase;
        letter-spacing: 1px;
        line-height: 50px;
      }
    }

    &-social {
      display: flex;
      align-items: center;

      &--fb {
        background-image: linear-gradient(-179deg, #4C79D3 2%, #224A9B 94%);

        svg {
          fill: #fff;
        }
      }
      &--ln {
        background-image: linear-gradient(-179deg, #2198D3 3%, #107FB7 95%);

        svg {
          fill: #fff;
        }
      }
      &--tw {
        background-image: linear-gradient(-179deg, #4FBAFD 5%, #2AA3EF 92%);

        svg {
          fill: #fff;
        }
      }
      &--em {
        background-image: linear-gradient(-179deg, #4B4B4B 7%, #000000 91%);

        svg {
          fill: #fff;
        }
      }
      &--sh {
        background-image: linear-gradient(-179deg, #FFFFFF 7%, #FDFDFD 88%);
        box-shadow: 0 2px 10px 0 rgba(0,68,157,0.10);

        svg {
          fill: #000;
        }
      }

      > a {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 45px;
        height: 45px;
        border-radius: 100%;
        margin-right: 10px;

        &:last-of-type {
          margin-right: 0;
        }
      }
    }
  }

  &__similar {
    padding-bottom: 171px;

    .exchequer__block {
      margin-bottom: 0;
    }

    &-title {
      padding-bottom: 30px;
      font-size: 26px;
      color: #000000;
      letter-spacing: 0;
      line-height: 50px;
    }
  }
}

// SECTION facilities :: END